<template>
  <div class="wsy_box">
    <!-- table -->
    <el-card class="box-card">
      <div class="el-card_body">
        <!--  -->
        <div class="wsy_top_search">
          <div class="top_search_left">
            <el-input
              placeholder=""
              v-model="getOrdersListParams.keyword"
              @clear="clear()"
              clearable
            ></el-input>
            <el-button icon="el-icon-search" @click="findOrder()"></el-button>
          </div>
        </div>
        <!--  -->
        <el-table :data="OrdersTable" stripe border style="width: 100%">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="order_number" label=""></el-table-column>
          <el-table-column prop="order_price" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-tag effect="dark" v-if="scope.row.pay_status !=0"></el-tag>
              <el-tag effect="dark" v-if="scope.row.pay_status == 0" type="danger"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="is_send" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">{{ scope.row.update_time | timestampToTime }}</template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <div>
                <el-tooltip :enterable="false" content="" placement="top">
                  <el-button
                    size="small"
                    type="primary"
                    icon="el-icon-edit"
                    @click="editOrdersMotai()"
                  ></el-button>
                </el-tooltip>
                <el-tooltip :enterable="false" content="" placement="top">
                  <el-button
                    size="small"
                    type="success"
                    icon="el-icon-location-outline"
                    @click="showlogisticsMotal(scope.row)"
                  ></el-button>
                </el-tooltip>
              </div>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="getOrdersListParams.pageNum"
          :page-sizes="[1,2,5,10]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalNum"
        ></el-pagination>
      </div>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editOrderMotal">
      <el-form
        :model="editOrderformData"
        :rules="editrules"
        ref="editForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="/" prop="cityname">
          <el-cascader
            :options="cityOptions"
            v-model="editOrderformData.value"
            @change="changeProvince"
            clearable
          ></el-cascader>
        </el-form-item>

        <el-form-item label="" prop="cityname">
          <el-input v-model="editOrderformData.cityname"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="quitEdit()"> </el-button>
        <el-button type="primary" @click="quitEdit()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="logisticsMotal">
      <div>
        <!--  -->
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :type="activity.type"
            :timestamp="activity.time"
          >{{activity.context}}</el-timeline-item>
        </el-timeline>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="logisticsMotal = false"> </el-button>
        <el-button type="primary" @click="logisticsMotal = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import cityOptions from "@/assets/provinces-china-master/city_data2017_element";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      // 
      getOrdersListParams: {
        keyword: "",
        pagenum: 1,
        pagesize: 10
      },
      totalNum: 0,
      OrdersTable: [],
      //   ID
      removeOrderID: 0,
      formLabelWidth: "120px",
      // 
      editOrderMotal: false,
      // 
      logisticsMotal: false,
      // 
      editOrderformData: {
        cityname: "",
        value: ""
      },
      cityOptions: cityOptions,
      // 
      editrules: {
        cityname: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      progressInfo:[]
    };
  },
  created() {},
  mounted() {
    this.getOrdersList();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 
    async getOrdersList() {
      let { data } = await this.$axios.get(
        `/orders?query=${this.getOrdersListParams.keyword}&pagenum=${this.getOrdersListParams.pagenum}&pagesize=${this.getOrdersListParams.pagesize}`
      );
      // console.log(data.data);

      this.totalNum = data.data.total;
      this.getOrdersListParams.pagenum = data.data.pagenum;
      this.OrdersTable = data.data.goods;
    },
    handleSizeChange(val) {
      this.getOrdersListParams.pagesize = val;
      this.getOrdersList();
      // console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.getOrdersListParams.pagenum = val;
      this.getOrdersList();
      // console.log(`: ${val}`);
    },
    // ID
    findOrder() {
      this.getOrdersListParams.keyword = "";
      this.getOrdersList();
    },
    // 
    clear() {
      this.getOrdersList();
    },
    // 
    editOrdersMotai() {
      this.editOrderMotal = true;
    },
    changeProvince() {},
    // 
    quitEdit() {
      this.editOrderMotal = false;
      this.editOrderformData.cityname = "";
      this.editOrderformData.value = "";
    },
    // 
    async showlogisticsMotal() {
      const { data } = await this.$axios.get("kuaidi/1106975712662");
      if (data.meta.status == 200) {
        this.editUserMotal = false;
        this.$message({
          message: "",
          type: "success"
        });
      } else {
        this.$message({
          message: "",
          type: "error"
        });
      }
      this.progressInfo = data.data
      this.logisticsMotal = true;
    }
  },
  filters: {
    // 
    timestampToTime(val) {
      let date = new Date(val * 1000); //10*1000131000
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D =
        (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
      let h =
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
      let m =
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        ":";
      let s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      return Y + M + D + h + m + s;
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='scss' scoped>
.box-card {
  margin-top: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
  border: 1px solid #ebeef5;
  background-color: #fff;
  color: #303133;
  transition: 0.3s;
  border-radius: 4px;
  overflow: hidden;
  > .el-card_body {
    padding: 20px;
  }
}
.wsy_top_search {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  > .top_search_left {
    display: flex;
    width: 40%;
    margin-right: 20px;
  }
}
.wsy_table {
  font-size: 12px;
  margin-top: 20px;
  width: 100%;
  border: 1px #efefef;
  color: #606266;
  tr {
    > td {
      box-sizing: border-box;
      line-height: 40px;
      padding-left: 10px;
    }
  }
}
.el-pagination {
  margin-top: 20px;
}
.el-form-item__label {
  text-align: left;
}
.settingOrder > p {
  padding-left: 20px;
}
.el-table {
  margin-top: 20px;
  font-size: 12px;
}
.el-cascader {
  width: 100%;
}
</style>
